@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@500&display=swap');

.HeaderText .sloganText, h2, .description {
  font-family: 'Orbitron', sans-serif;
  color: #fff;
  text-align: left;
}


.HeaderText {
  padding-top: 8%;
  padding-bottom: 3%;
  max-width: 90%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: start;
  grid-gap: 40px;


  .sloganText {
    text-shadow: whitesmoke 0 0 20px;
    font-size: 45px;
    font-weight: 500;
  }
  h2{
    letter-spacing: 3px;
  }


    button {
     // margin-top: 30px;
      //margin-bottom: 70px;
      padding: 12px;
      border: none;
      background: linear-gradient(135deg, cyan, deepskyblue) ;
      color: whitesmoke;
      font-style: normal;
      font-size: 14px;
      border-radius: 5px;
      cursor: pointer;
      transition: .3s;
      font-family: 'Manrope', sans-serif;
    }


}


@media screen and (max-width: 735px) {
  .HeaderText .sloganText, h2, .description {
    text-align: center;
  }


  .HeaderText {
    padding: 100px 10px 6% 10px;
    align-items: center;

    .sloganText {
      font-size: 30px;
    }

    h2 {
      font-size: 15px;
    }
  }
}


@media screen and (max-width: 450px) {

  .HeaderText {
    grid-gap: 25px;
    .sloganText {
      font-size: 25px;
    }
    .buttons {
      grid-gap: 10px;

      button {
        font-size: 12px;
      }
    }
  }
}


@media screen and (max-width: 273px) {

  .HeaderText {
    .sloganText {
      font-size: 20px;
    }
    .buttons {
      grid-gap: 5px;

      button {
        font-size: 10px;
      }
    }
  }
}

